import { FocusEvent } from 'react';
import { TextFieldProps } from '../../atoms/input';

export interface DatepickerProps {
  qa?: string;
  format?: string;
  mask?: string;
  inputProps?: Pick<TextFieldProps, 'id' | 'label' | 'description' | 'disabled' | 'name' | 'required' | 'size'>;
  value?: string;
  invalidDateText?: string;
  iconButtonLabel?: string;
  calendarProps?: DatepickerCalendarProps;
  onChange?: (value: string) => void;
}

export interface CalendarProps {
  ariaLabel?: string;
  ariaLabelCurrentDay?: string;
  ariaLabelNextMonth?: string;
  ariaLabelNextYear?: string;
  ariaLabelNextYears?: string;
  ariaLabelPreviousMonth?: string;
  ariaLabelPreviousYear?: string;
  ariaLabelPreviousYears?: string;
  className?: string;
  isOpen?: boolean;
  onBlur?: (e: FocusEvent) => void;
  onChange?: (value: string) => void;
  qa?: string;
  unavailable?: string[];
  unavailableFrom?: string;
  unavailableTo?: string;
  value?: string;
}

type DatepickerCalendarProps = Pick<
  CalendarProps,
  | 'ariaLabel'
  | 'ariaLabelCurrentDay'
  | 'ariaLabelNextMonth'
  | 'ariaLabelNextYear'
  | 'ariaLabelNextYears'
  | 'ariaLabelPreviousMonth'
  | 'ariaLabelPreviousYear'
  | 'ariaLabelPreviousYears'
  | 'qa'
  | 'unavailable'
  | 'unavailableFrom'
  | 'unavailableTo'
>;

export interface MonthsViewProps {
  value?: Date;
  onChange: (value: Date) => void;
  activeYear: number;
}

export interface YearsViewProps {
  value?: Date;
  onChange: (value: Date) => void;
  activeMonth: number;
  yearsRowsStart: number;
}

export type DaysViewProps = Pick<
  CalendarProps,
  'ariaLabelCurrentDay' | 'unavailableFrom' | 'unavailableTo' | 'unavailable'
> & {
  value?: Date;
  onChange: (value: Date) => void;
  activeMonth: number;
  activeYear: number;
};

export type DayButtonProps = Pick<
  DaysViewProps,
  'ariaLabelCurrentDay' | 'unavailableFrom' | 'unavailableTo' | 'unavailable' | 'onChange' | 'value'
> & {
  date: Date;
  monthYear: Date;
};

export enum CalendarView {
  DAYS = 'd',
  MONTHS = 'm',
  YEARS = 'y'
}
